import React, { Component, useState } from 'react';
import { Table, Input, InputNumber, Button, Form, Modal, message, Row, Col, Menu, Select, DatePicker } from 'antd';
import { EditOutlined, DeleteOutlined, PlusCircleOutlined, ExclamationCircleOutlined, CarOutlined, BgColorsOutlined, FileExcelFilled } from '@ant-design/icons';
import Flex from 'components/shared-components/Flex'
import { DataService } from "services/trucks.service";
import { DataService as FuelService } from "services/fuel.service";
import { Typography } from "antd";
import EllipsisDropdown from 'components/shared-components/EllipsisDropdown';
import NumberFormat from 'react-number-format';
import { useHistory } from "react-router-dom";
import moment from 'moment';
import Invoice from './invoice';

const { RangePicker } = DatePicker;
const { Title } = Typography;
const dateFormat = 'DD/MM/YYYY';
const { confirm } = Modal;
const { Option } = Select;

const EditableTable = ({systemData}) => {
  const [form] = Form.useForm();
  const [editingKey, setEditingKey] = useState('');
  const [visible, setVisible] = useState(false);
  const [showDriver, setShowDriver] = useState(false);
  const [showFuel, setShowFuel] = useState(false);
  const [currentDriver, setCurrentDriver] = useState();
  const [currentTruck, setCurrentTruck] = useState('');
  const [liter, setLiter] = useState(0);
  const [rate, setRate] = useState(0);
  const [price, setPrice] = useState(0);
  const [totalAmount, setTotalAmount] = useState(0);
  const [confirmLoading, setConfirmLoading] = useState(false);
  const [assignLoading, setAssignLoading] = useState(false);
  const [reportModal, setReportModal] = useState(false);
  const [dates, setDates] = useState(new Date());
  const [allTrips, setAllTrips] = useState([]);
  const [loading, setLoading] = useState(false);
	const [loaded, setLoaded] = useState(false);
  const [ fdate, setFdate ] = useState(0)
  const [ tdate, setTdate ] = useState(0)
  const [reportData, setReportData] = useState([]);
  const [roadTalls, setRoadTalls] = useState([]);
	const [orderGarage, setOrderGarage] = useState([]);
	const [orderFuel, setOrderFuel] = useState([]);

  const { allDestinationsLoading, allDestinations, drivers, allEmployees, allTrucks, trucks, trucksLoading,  countries, countriesLoading, currencies, currenciesLoading, trips, tripsLoading,  orders, ordersLoading, roadTall, roadTallLoading, fuels, fuelsLoading, rounder, garages, garagesLoading, allTripsLoading:tripDataLoading, allTrips:tripData, allBorders, allCurrencyLoading, allCurrency, allCountries, allCountriesLoading} = systemData;
  let history = useHistory();

  const rangeConfig = {
		rules: [{ type: 'array', required: true, message: 'Please select time!' }],
	};

  let allRecords = [];
  	if(!ordersLoading){
		orders.docs.map((doc)=>{
			allRecords = {
				...allRecords,
				[doc.data()._id] : {'client':doc.data().client, 'item':doc.data().item, 'departure':doc.data().departure, 'destination':doc.data().destination},
			}
		})
	}

  let tripOrders = [];
  	if(!tripsLoading){
    trips.docs.map((doc)=>{
			tripOrders = {
				...tripOrders,
				[doc.data()._id] : doc.data().orderId,
			}
		})
	}

  const viewDetails = row => {
    
    setEditingKey(row._id);
    setCurrentTruck(allTrucks[+row._id] ? `${allTrucks[row._id].model} ${allTrucks[row._id].plateNumber}` : '');
    setVisible(true);
    form.setFieldsValue({
      model:row.model,
      plateNumber:row.plateNumber,
      trailerNumber:row.trailerNumber,
      //meterNumber:row.meterNumber,
      openingFuel:row.openingFuel,
    });
  }

  const handleDateChange = date => {
		setDates(date ? new Date(date.format('YYYY-MM-DD HH:mm:ss')) : new Date());
	};

  const disabledDate = (current) => {
		return current && current > moment().endOf("day");
	} 

  

  const dropdownMenu = row => (
		<Menu>
			<Menu.Item onClick={() => {changeDriver(row)}}>
				<Flex alignItems="center">
					<CarOutlined />
					<span className="ml-2">Change driver</span>
				</Flex>
			</Menu.Item>
      <Menu.Item onClick={() => {addFuel(row)}}>
				<Flex alignItems="center">
					<BgColorsOutlined />
					<span className="ml-2">Add Fuel</span>
				</Flex>
			</Menu.Item>
      <Menu.Item onClick={() => {handleReportModal(row)}}>
				<Flex alignItems="center">
					<FileExcelFilled />
					<span className="ml-2">Trips Report</span>
				</Flex>
			</Menu.Item>
			<Menu.Item onClick={() => {viewDetails(row)}}>
				<Flex alignItems="center">
					<EditOutlined />
					<span className="ml-2">Edit Details</span>
				</Flex>
			</Menu.Item>
			<Menu.Item onClick={() => confirmDelete(row)}>
				<Flex alignItems="center">
					<DeleteOutlined />
					<span className="ml-2">Delete</span>
				</Flex>
			</Menu.Item>
		</Menu>
	);

  const confirmDelete = (row) => {
    confirm({
      title: 'Do you Want to delete this truck?',
      icon: <ExclamationCircleOutlined />,
      onOk() {
        remove(row._id);
      }
    });
  };

  const handleReportModal = (row) => {
    setCurrentTruck(allTrucks[+row._id] ? `${allTrucks[row._id].model} ${allTrucks[row._id].plateNumber}` : '');
    setEditingKey(row._id);
    setReportModal(true);
  };

  const handleReport = async () => {
		try {
			setLoading(true);
			const fieldsValue = await form.validateFields();
			let rangeValue = fieldsValue['range-picker'];
			let fdate = new Date(rangeValue[0].format('YYYY-MM-DD')).setHours(0,0,0,0);
			let tdate = new Date(rangeValue[1].format('YYYY-MM-DD')).setHours(23,59,59,999);
      setTdate(tdate);
			setFdate(fdate);
			let data = tripsLoading ? [] : trips.docs.filter(x => +x.data()._id >= fdate &&  +x.data()._id <= tdate && x.data().truckId == +editingKey)
			let roadtll = roadTallLoading ? [] : roadTall.docs.filter(x => +x.data()._id >= fdate &&  +x.data()._id <= tdate && x.data().truckId == +editingKey)
			let garage = garagesLoading ? [] : garages.docs.filter(x => +x.data()._id >= fdate &&  +x.data()._id <= tdate && x.data().truckId == +editingKey)
			let fuel = fuelsLoading ? [] : fuels.docs.filter(x => +x.data()._id >= fdate &&  +x.data()._id <= tdate && x.data().truckId == +editingKey)

      setRoadTalls(roadtll);
      setOrderGarage(garage);
      setOrderFuel(fuel);
			setReportData(data);
			setReportModal(false);
			setLoading(false);
			setLoaded(true);
		} catch (errInfo) {}
	};
	
  const changeDriver = (row) => {
    setCurrentTruck(allTrucks[+row._id] ? `${allTrucks[row._id].model} ${allTrucks[row._id].plateNumber}` : '');
    setCurrentDriver(row.driverId);
    setEditingKey(row._id);
    setShowDriver(true);
  };
	
  const addFuel = (row) => {
    setCurrentTruck(allTrucks[+row._id] ? `${allTrucks[row._id].model} ${allTrucks[row._id].plateNumber}` : '');
    setEditingKey(row._id);
    let trps = tripsLoading ? [] : trips.docs.filter(x => x.data().truckId == row._id && x.data().status == 'En Route');
    setAllTrips(trps);
    setShowFuel(true);
  };



  const showModal = () => {
    setVisible(true);
  };

  const handleOk = async e => {
    try {
      const values = await form.validateFields();
      let model = values.model.slice(0,1) === ' ' ? values.model.slice(1) : values.model;
      let plateNumber = values.plateNumber.slice(0,1) === ' ' ? values.plateNumber.slice(1) : values.plateNumber;
      let trailerNumber = values.plateNumber.slice(0,1) === ' ' ? values.trailerNumber.slice(1) : values.trailerNumber;
      //let meterNumber = values.plateNumber.slice(0,1) === ' ' ? values.meterNumber.slice(1) : values.meterNumber;
      let openingFuel = values.plateNumber.slice(0,1) === ' ' ? values.openingFuel.slice(1) : values.openingFuel;
      let verifyPlate = '';
      let verifyTrailer = '';
      if (model =='' || plateNumber == '' || trailerNumber == '' || openingFuel == '') return;

      if(!editingKey){
        verifyPlate = trucksLoading ? [] : trucks.docs.filter( truck => truck.data().plateNumber === plateNumber);
		    verifyTrailer = trucksLoading ? [] : trucks.docs.filter( truck => truck.data().trailerNumber === trailerNumber);
      }
      else{
        verifyPlate = trucksLoading ? [] : trucks.docs.filter( truck => truck.data().plateNumber == plateNumber && +truck.data()._id !== +editingKey);
		    verifyTrailer = trucksLoading ? [] : trucks.docs.filter( truck => truck.data().trailerNumber == trailerNumber && +truck.data()._id !== +editingKey);
      }

      if(verifyPlate.length > 0){
        message.error('Plate number already registered');
        return;
      }
      if(verifyTrailer.length > 0){
        message.error('Trailer plate number already registered');
        return;
      }

      setConfirmLoading(true);
      let truckData = { model,plateNumber,trailerNumber,openingFuel,driverId:'',driverName:'' }
      if(!editingKey) DataService.create(truckData);
      else{
        DataService.update(editingKey,truckData)
        setEditingKey('');
        message.success(`Truck info updated`);
      }
      handleCancel();
    } catch (errInfo) {}
    
  };

  const handleAddFuel = async e => {
    try {
      const values = await form.validateFields();
      // let meterNumber = values.meterNumber;
      let liters = values.liters;
      let literPrice = values.literPrice;
      let station = ''+values.station.slice(0,1) === ' ' ? ''+values.station.slice(1) : values.station;
      let country = values.country;
      let currency = values.currency;
      let exchangeRate = values.exchangeRate;
      let tripId = +values.tripId;
      let orderId = +tripId ? tripOrders[tripId] : '';
      if (liters === '' || literPrice === '' || station === '' || country === '' || currency === '' || !editingKey || !totalAmount) return;
      // let verifyMeter = allTrucks[+editingKey].meterNumber > +meterNumber;

      // if(verifyMeter){
      //   message.error('Meter number can not be less than meter number in truck list');
      //   return;
      // }
      setConfirmLoading(true);
      let fuelData = { liters,literPrice,station,country,exchangeRate,truckId:editingKey, currency, totalAmount, tripId, orderId, internal:true }
      FuelService.create(fuelData,+(dates.getTime()));
      handleClose();
      history.push(`/app/apps/fuels`)
      message.success(`Fuel info added`);
      
      handleCancel();
    } catch (errInfo) {}
    
  };


  const driverSelected = (value) => {
    setCurrentDriver(value);
  }

  const onDriverChange = () => {
    if(!editingKey) return;
    setAssignLoading(true)
    DataService.update(editingKey,{driverId:currentDriver,driverName:allEmployees[currentDriver].name})
    handleClose();
    setAssignLoading(false);
    message.success(`Truck driver info updated`);
  }

  const handleCancel = () => {
    setVisible(false);
    setConfirmLoading(false);
    setCurrentTruck('');
    form.setFieldsValue({model:null,plateNumber:null,trailerNumber:null,meterNumber:null,openingFuel:null});
  };

  const handleClose = () => {
    setShowDriver(false);
    setShowFuel(false);
    setReportModal(false);
    setLoaded(false);
    setCurrentDriver();
    setTotalAmount(0);
    setEditingKey();
  }

  const handleRateChange = (value) => {
    let total = value*liter*price;
    setRate(value);
    setTotalAmount(total);
  }

  const handleLiterChange = (value) => {
    let total = rate*price*value;
    setLiter(value);
    setTotalAmount(total);
  }

  const handlePriceChange = (value) => {
    let total = rate*liter*value;
    setPrice(value);
    setTotalAmount(total);
  }
  
  const remove = key => {
    DataService.remove(key)
    message.success(`Truck info deleted`);
  };

  const columns = [
    {
      title: 'Truck Model',
      dataIndex: 'model',
      render: (_, record) => {
        return record.data().model
      }
    },
    {
      title: 'Plate Number',
      dataIndex: 'plateNumber',
      render: (_, record) => {
        return record.data().plateNumber
      }
    },
    {
      title: 'Trailer Number',
      dataIndex: 'trailerNumber',
      render: (_, record) => {
        return record.data().trailerNumber
      }
    },
    {
      title: 'Opening Fuel',
      dataIndex: 'openingFuel',
      render: (_, record) => (
				<div>
					<NumberFormat
						displayType={'text'} 
						value={record.data().openingFuel} 
						suffix={'L'} 
						thousandSeparator={true} 
					/>
				</div>
			),
    },
    {
      title: 'Driver name',
      dataIndex: 'driverId',
      render: (_, record) => {
        return record.data().driverId ? (allEmployees[record.data().driverId] ? allEmployees[record.data().driverId].name : record.data().driverId ) : ''
      }
    },
    {
			title: '',
			dataIndex: 'actions',
			render: (_, elm) => (
				<div className="text-right">
					<EllipsisDropdown menu={dropdownMenu(elm.data())}/>
				</div>
			)
		}
  ];
  return (
    <Form form={form} component={false} layout="vertical">
      {!loaded ? (<>
      <Flex alignItems="center" justifyContent="between" mobileFlex={false}>
        <Flex className="mb-1" mobileFlex={false}>
          <div className="mr-md-3">
            <Title level={4}>Trucks List</Title>
          </div>
        </Flex>
        <Flex mobileFlex={false}>
          <div>
            <Button size={'small'} 
                    style={{
                      marginTop: 0,
                      paddingTop: 0,
                    }}
                    onClick={showModal}
                    type="primary" icon={<PlusCircleOutlined />} block>Add New</Button>
            <Modal
              title={editingKey ? "Edit Truck details" : "New Truck details"}
              visible={visible}
              onOk={handleOk}
              confirmLoading={confirmLoading}
              onCancel={handleCancel}
            >
              <Row gutter={16}>
                <Col xs={24} sm={24} md={24}>
                  <Form.Item name="model" label="Truck model" rules={[
                    {
                      required: true,
                      message: `model is required.`,
                    },
                  ]}>
                    <Input/>
                  </Form.Item>
                </Col>
                <Col xs={24} sm={24} md={24}>
                  <Form.Item name="plateNumber" label="Plate Number" rules={[
                    {
                      required: true,
                      message: `Plate Number is required.`,
                    },
                  ]}>
                    <Input
                      className="w-100"                      
                    />
                  </Form.Item>
                </Col>
                <Col xs={24} sm={24} md={24}>
                  <Form.Item name="trailerNumber" label="Trailer Number" rules={[
                    {
                      required: true,
                      message: `Trailer Number is required.`,
                    },
                  ]}>
                    <Input
                      className="w-100"                      
                    />
                  </Form.Item>
                </Col>
                <Col xs={24} sm={24} md={24}>
                  <Form.Item name="openingFuel" label="Fuel in tank (in liters)" rules={[
                    {
                      required: true,
                      message: `Fuel in tank is required.`,
                    },
                  ]}>
                    <InputNumber
                      className="w-100"
                      min={0}
                      formatter={value => `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, '')}
                      parser={value => value.replace(/\$\s?|(,*)/g, '')}
                    />
                  </Form.Item>
                </Col>
              </Row>
            </Modal>        
            <Modal
              title={`Change truck driver ${currentTruck}`}
              visible={showDriver}
              onOk={onDriverChange}
              confirmLoading={assignLoading}
              onCancel={handleClose}
            >
              <Row gutter={16}>
                <Col xs={24} sm={24} md={24}>
                  <Form.Item
                    label="Driver Name"
                    name="driverId"
                    rules={[{ required: true,message: `Select driver for the truck` }]}
                    style={{ display: 'block'}}
                    className="w-100" 
                  >
                    <Select
                      className="w-100"
                      placeholder="-Select-"
                      onChange={driverSelected}
                      value={currentDriver}
                    >
                        {drivers.map((driver)=><Option value={driver._id}>{driver.name}</Option>)}
                    </Select>
								  </Form.Item>
                </Col>
              </Row>
            </Modal> 

            <Modal
              title={`Generate report for ${currentTruck}`}
              visible={reportModal}
              onOk={handleReport}
              confirmLoading={loading}
              onCancel={handleClose}
            >
              <Row gutter={16}>
                <Col xs={24} sm={24} md={24}>
                  <Form.Item name="range-picker" label="Pick Dates" {...rangeConfig}>
                    <RangePicker className="w-100" disabledDate={disabledDate}/>
                  </Form.Item>
                </Col>
              </Row>
            </Modal> 

            <Modal
              title={`Add fuel in truck ${currentTruck}`}
              visible={showFuel}
              onOk={handleAddFuel}
              onCancel={handleClose}
            >
              <Row gutter={16}>
                <Col xs={24} sm={24} md={24}>
                  <Form.Item
                    label="Specify Fuel for what"
                    name="tripId"
                    rules={[{ required: true,message: `Specification is required` }]}
                    style={{ display: 'block'}}
                    className="w-100" 
                  >
                    <Select
                      className="w-100"
                      placeholder="-Select-"
                    >
                        <Option value='0'>Internal Use</Option>
                        {allTrips.map((trip)=><Option value={trip.data()._id}>
                          {allDestinationsLoading ? '' : allDestinations[allRecords[trip.data().orderId]?.departure]?.name +' - '+allDestinations[allRecords[trip.data().orderId]?.departure]?.country}
                          {' to '} 
                          {allDestinationsLoading ? '' : allDestinations[allRecords[trip.data().orderId]?.destination]?.name +' - '+allDestinations[allRecords[trip.data().orderId]?.destination]?.country}
                          {' with '+trip.data().milleage+' RWF'}
                        </Option>)}
                    </Select>
								  </Form.Item>
                </Col>
                <Col xs={24} sm={12} md={12}>
                  <Form.Item name="dates" label="Date added" rules={[
                    {
                      required: true,
                      message: `When fuel were added?`,
                    },
                  ]}>
                    <DatePicker className="w-100" defaultValue={moment(dates, dateFormat)} format={dateFormat} disabledDate={disabledDate} onChange={handleDateChange}/>
                  </Form.Item>
                </Col>
                
                <Col xs={24} sm={12} md={12}>
                  <Form.Item name="liters" label="Liters added" rules={[
                    {
                      required: true,
                      message: `How many liters were added`,
                    },
                  ]}>
                    <InputNumber
                      className="w-100"
                      min={0}
                      onChange={handleLiterChange}
                      formatter={value => `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, '')}
                      parser={value => value.replace(/\$\s?|(,*)/g, '')}
                    />
                  </Form.Item>
                </Col>
                <Col xs={24} sm={12} md={12}>
                  <Form.Item name="station" label="Station" rules={[
                    {
                      required: true,
                      message: `station is required.`,
                    },
                  ]}>
                    <Input
                      className="w-100"                      
                    />
                  </Form.Item>
                </Col>
                <Col xs={24} sm={12} md={12}>
                  <Form.Item name="country" label="Select which country" rules={[
                    {
                      required: true,
                      message: `Country is required.`,
                    },
                  ]}>
                    <Select
                      className="w-100 d-block"
                      placeholder="- Select Country -"
                    >
                        {!countriesLoading ? countries.docs.map((country)=><Option value={country.data()._id}>{country.data().name}</Option>) : null }
                    </Select>
                  </Form.Item>
                </Col>
                <Col xs={24} sm={12} md={12}>
                  <Form.Item name="literPrice" label="Price per liter" rules={[
                    {
                      required: true,
                      message: `Price per liter is required.`,
                    },
                  ]}>
                    <InputNumber
                      className="w-100"
                      min={0}
                      onChange={handlePriceChange}
                      formatter={value => `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, '')}
                      parser={value => value.replace(/\$\s?|(,*)/g, '')}
                    />
                  </Form.Item>
                </Col>
                <Col xs={24} sm={12} md={12}>
                  <Form.Item name="currency" label="Select currency used" rules={[
                    {
                      required: true,
                      message: `Currency is required.`,
                    },
                  ]}>
                    <Select
                      className="w-100 d-block"
                      placeholder="- Select Currency -"
                    >
                        {!currenciesLoading ? currencies.docs.map((currency)=><Option value={currency.data()._id} className='w-100 pe-5'>{currency.data().name}&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</Option>) : null }
                    </Select>
                  </Form.Item>
                </Col>
                <Col xs={24} sm={12} md={12}>
                  <Form.Item name="exchangeRate" label="Exchange Rate vs 1RWF" rules={[
                    {
                      required: true,
                      message: `rate is required.`,
                    },
                  ]}>
                    <InputNumber
                      className="w-100"
                      min={0}
                      onChange={handleRateChange}
                      formatter={value => `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, '')}
                      parser={value => value.replace(/\$\s?|(,*)/g, '')}
                    />
                  </Form.Item>
                </Col>
                <Col xs={24} sm={12} md={12}>
                  <Title level={4}>
                    <NumberFormat
                      displayType={'text'} 
                      value={totalAmount}
                      suffix={'RWF'} 
                      prefix={'Total RWF: '} 
                      thousandSeparator={true} 
                    />
                    <Form.Item >
                      <Input
                        className="w-100 mt-2" value={totalAmount}                      
                      />
                    </Form.Item>
                  </Title>
                </Col>
              </Row>
            </Modal>  
          </div>
        </Flex>
      </Flex>
      <Table
        dataSource={trucksLoading ? [] : trucks.docs} 
        columns={columns}
        loading={trucksLoading}
        rowClassName="editable-row"
        size="middle"
      />
      </>)
       : (<Invoice 
        orderTrips={reportData} 
        fdate={fdate} 
        tdate={tdate} 
        setLoaded={setLoaded} 
        currentTruck={currentTruck} 
        tripDataLoading={tripDataLoading}
				tripData={tripData}
				rounder={rounder}
        allCurrencyLoading={allCurrencyLoading} 
        allCurrency={allCurrency}
        allDestinationsLoading={allDestinationsLoading}
        allDestinations={allDestinations}
        allTrucks={allTrucks}
        allBorders={allBorders}
        allCountries={allCountries}
				allCountriesLoading={allCountriesLoading}
        roadTalls={roadTalls}
        orderFuel={orderFuel}
        orderGarage={orderGarage}
      />)}
    </Form>
  );
};

export class Stores extends Component {
  render() {
    return (
      <EditableTable systemData={this.props.systemData}/>
    )
  }
}

export default Stores
